import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsPassbookListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-container-lookup-option/ps-passbook-list/ps-passbook-list.component';
import { IOptionsPsInputFreeTextExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-free-text/ps-input-free-text.component.interfaces';
import { IOptionsPsInputVarcharExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { IOptionsPsLookupChequebooksExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-chequebooks/ps-lookup-chequebooks.component.interfaces';
import { IOptionsPsLookupCreditCardsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-credit-cards/ps-lookup-credit-cards.component.interfaces';
import { IOptionsPsLookupDebitCardsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-debit-cards/ps-lookup-debit-cards.component.interfaces';
import { IOptionsPsLookupPassbooksExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-passbooks/ps-lookup-passbooks.component.interfaces';
import { IOptionsPsDropdownDocumentTypesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-document-types/ps-dropdown-document-types.component.interfaces';
import { IOptionsPsDropdownLov } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-lov/ps-dropdown-lov.component.interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsContainerPanel, IOptionsPsKeyinInputExposed, IOptionsPsSelectDropdown, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsComplexAmountExposed } from '../../commonBussinessSRC/psComponents/ps-complex-components/ps-complex-amount/ps-complex-amount.component.interfaces';
import { OmniBasePage } from '../omni-base/omni-base.page';
import { ConstantCommon } from './../../commonSRC/psServices/models/common-constant';

@Component({
  selector: 'app-lost-document-request',
  templateUrl: './lost-document-request.page.html',
  styleUrls: ['./lost-document-request.page.scss'],
})
export class LostDocumentRequestPage extends OmniBasePage implements OnInit {
  formGroup: FormGroup = new FormGroup({});
  isCurrAmountHidden: boolean;
  lostDocumentRequestVO: any = {};
  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'lost_doc_stepper',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['lost_doc_req_step1'],
    group: this.formGroup,
    requestObject: this.lostDocumentRequestVO,
    submitOptions: {
      extraParams: {},
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.debitCardRequest,
      group: this.formGroup,
    }
  };
  lostDocumentTypesOptions: IOptionsPsDropdownDocumentTypesExposed = {
    labelKey: 'lost_document_type_key',
    placeHolder: 'please_slct_lost_doc_type_key',
    fcName: 'documentType',
    group: this.formGroup,
    iconOptions: {
      iconName: 'clipboard'
    }
  }

  serialNumberOptions: IOptionsPsInputVarcharExposed = {
    group: this.formGroup,
    fcName: 'serialNo',
    labelKey: 'serial_no_key',
    placeHolder: 'enter_serial_no_key',
    iconOptions: {
      iconName: 'clipboard'
    }
  };
  documentCurrencyOptions: IOptionsPsKeyinInputExposed = {
    placeHolder: 'document_currency_key',
    labelKey: 'document_currency_key',
    group: this.formGroup,
    fcName: 'documentCurrency'
  };
  documentNumberOptions: IOptionsPsInputVarcharExposed = {
    group: this.formGroup,
    fcName: 'documentNumber',
    labelKey: 'document_number_key',
    placeHolder: 'enter_document_number_key',
    iconOptions: {
      iconName: 'clipboard'
    },
  };
  fromDocumentNumberOptions: IOptionsPsInputVarcharExposed = {
    group: this.formGroup,
    fcName: 'fromdocumentNumber',
    labelKey: 'from_document_number_key',
    placeHolder: 'enter_from_document_number_key',
    iconOptions: {
      iconName: 'clipboard'
    }
  };
  toDocumentNumberOptions: IOptionsPsInputVarcharExposed = {
    group: this.formGroup,
    fcName: 'todocumentNumber',
    labelKey: 'to_document_number_key',
    placeHolder: 'enter_to_document_number_key',
    iconOptions: {
      iconName: 'clipboard'
    }
  };

  passcodeCommentOptions: IOptionsPsInputVarcharExposed = {
    group: this.formGroup,
    fcName: 'passcodeComment',
    labelKey: 'comment_key',
    placeHolder: 'enter_comment_key',
    iconOptions: {
      iconName: 'clipboard'
    }
  };

  stopReasonOptions: IOptionsPsSelectDropdown = {
    labelKey: 'stop_reason_key',
    placeHolder: 'please_select_stop_reason_key',
    fcName: 'stopReason',
    group: this.formGroup,
    iconOptions: {
      iconName: 'clipboard'
    }
  }
  stopReasonsOptions: IOptionsPsSelectDropdown = {
    labelKey: 'stop_reason_key',
    placeHolder: 'please_select_stop_reason_key',
    fcName: 'stopReason',
    group: this.formGroup,
    iconOptions: {
      iconName: 'clipboard'
    },
  }
  briefExplanationOptions: IOptionsPsInputFreeTextExposed = {
    labelKey: 'brief_explanation_key',
    placeHolder: 'enter_brief_explanation_key',
    fcName: 'briefExplanation',
    group: this.formGroup,
    iconOptions: {
      iconName: 'clipboard'
    }
  };

  public lostDocumentPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'lost_document_key',
    iconName: 'document',
    expanded: true
  };
  private documentTypeCategory: string;
  public showDocCurrency = false;
  public showDocSINo = false;
  public showDebitCardlookup = false;
  public showCreditCardlookup = false;
  public showChequebooklookup = false;
  public showPassbookslookup = false;
  public showCardTypelookup = false;
  private documentNumberRqd: number;
  remittanceTypes: any;
  public lostDocNoRequired = '';
  public requestedBranch = '';
  chequeBookLookupOptions: IOptionsPsLookupChequebooksExposed = {
    labelKey: 'chequebook_key',
    placeHolder: 'select_chequebook_key',
    group: this.formGroup,
    fcName: 'chequebook',
    requestObject: this.lostDocumentRequestVO
  };
  debitCardsLookupOptiops: IOptionsPsLookupDebitCardsExposed = {
    labelKey: 'debit_card_key',
    placeHolder: 'select_debit_card_key',
    group: this.formGroup,
    fcName: 'debitCard',
    requestObject: this.lostDocumentRequestVO,
    submitType: 0,
  };

  creditCardsLookupOptiops: IOptionsPsLookupCreditCardsExposed = {
    labelKey: 'credit_card_key',
    placeHolder: 'select_credit_card_key',
    group: this.formGroup,
    fcName: 'creditCard',
    requestObject: this.lostDocumentRequestVO,
    submitType: 0,
  };


  passbooksLookupOptions: IOptionsPsLookupPassbooksExposed = {
    labelKey: 'passbook_key',
    placeHolder: 'select_passbook_key',
    component: PsPassbookListComponent,
    group: this.formGroup,
    fcName: 'passbook',
    requestObject: this.lostDocumentRequestVO
  };

  cardsLookupOptiops: IOptionsPsDropdownLov = {
    fcName: 'cardType',
    group: this.formGroup,
  };

  complexChargesOptions: IOptionsPsComplexAmountExposed = {
    currency: '',
    disablePreview: true,
    requestObject: this.lostDocumentRequestVO,
    showLimitLabel: false,
    amountOptions: {
      fcName: 'chargesAmount',
      group: this.formGroup,
      labelKey: 'amount_key',
      placeHolder: 'amount_key'
    },
    currenciesOptions: {
      fcName: 'chargesCurrency',
      group: this.formGroup,
      labelKey: 'currency_key',
      placeHolder: 'currency_key'
    }
  };

  constructor(public commonProv: PsCommonService, public logger: LoggerService, private omniPullService: OmniPullService) {
    super();
  }


  ngOnInit() {
    super.init();
    this.resetVisibility();
    const pattern = "^[0-9]*$";
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_PATTERN, [this.documentNumberOptions.fcName], pattern);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_PATTERN, [this.toDocumentNumberOptions.fcName], pattern);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_PATTERN, [this.fromDocumentNumberOptions.fcName], pattern);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexChargesOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexChargesOptions.amountOptions.fcName], 1);
    this.baseFormGroup = this.formGroup;
    this.commonProv.setFormData(this.formGroup, this.lostDocumentRequestVO);
    const baseCurrency = this.commonProv.session.getValueOf(ConstantCommon.USERINFO).companySettingsCO.baseCurrencyISO;
    this.commonProv.setValInsideNestedObj(this.complexChargesOptions.currenciesOptions.fcName, baseCurrency, this.lostDocumentRequestVO);
    const amountFieldCust = this.commonProv.getElementValidations(this.complexChargesOptions.amountOptions.fcName);
    const currenciesFieldCust = this.commonProv.getElementValidations(this.complexChargesOptions.currenciesOptions.fcName);

    if(amountFieldCust.IS_VISIBLE === 0 || currenciesFieldCust.IS_VISIBLE === 0){
      this.isCurrAmountHidden = true;
    }else{
      this.isCurrAmountHidden = false;
    }
  }



  async resetVisibility() {
    this.showDebitCardlookup = false;
    //  this.showCardTypelookup = false;
    this.showCreditCardlookup = false;
    this.showChequebooklookup = false;
    this.showPassbookslookup = false;

    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [
      this.documentNumberOptions.fcName, this.cardsLookupOptiops.fcName, this.creditCardsLookupOptiops.fcName, this.debitCardsLookupOptiops.fcName, this.chequeBookLookupOptions.fcName, this.passbooksLookupOptions.fcName,
      this.fromDocumentNumberOptions.fcName, this.toDocumentNumberOptions.fcName, this.passcodeCommentOptions.fcName], 0);
  }

  onDocTypeChange(event) {
    this.resetVisibility().then(() => this.applyCustAfterSelectDoc(event));
    this.commonProv.setValInsideNestedObj(this.complexChargesOptions.amountOptions.fcName, "", this.lostDocumentRequestVO);
  }

  onLookupChange(event) {
    const chargesReqParam = {
      "commonParametersList": {
        "itemCard": event
      },
      "entityType": this.lostDocumentRequestVO.documentType?.description,
      "entityTypeCode": this.lostDocumentRequestVO.documentType?.selectedObj.docCode
    };
    this.commonProv.presentLoading();
    this.omniPullService.returnDocumentCharges(chargesReqParam).then((res: any) => {
      if (res) {
        this.commonProv.setValInsideNestedObj(this.complexChargesOptions.amountOptions.fcName, res.totalCharge, this.lostDocumentRequestVO);
        this.commonProv.dismissLoading();
      }
    }).catch(error => {
      this.commonProv.logger.error(error);
      this.commonProv.dismissLoading();
    });
  }

  applyCustAfterSelectDoc(event) {
    if (event != null && event !== undefined && event.selectedObj) {
      const docType = event.selectedObj.docCard;
      this.documentTypeCategory = docType;

      switch (docType) {
        case 'C':
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.documentNumberOptions.fcName, this.chequeBookLookupOptions.fcName], 1);
          this.lostDocumentRequestVO['submitType'] = '2';
          break;
        case 'Q':
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.fromDocumentNumberOptions.fcName, this.chequeBookLookupOptions.fcName, this.toDocumentNumberOptions.fcName], 1);
          this.lostDocumentRequestVO['submitType'] = '1';
          break;
        case 'D':
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.cardsLookupOptiops.fcName], 1);
          this.lostDocumentRequestVO['submitType'] = '3';
          break;
        case 'P':
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.passbooksLookupOptions.fcName], 1);
          this.showPassbookslookup = true;
          this.lostDocumentRequestVO['submitType'] = '4';
          break;
      }
    }
  }

  onChange(values: any) {
    if (values !== undefined) {
      if (values.itemValue === 'D') {
        this.showDebitCardlookup = true;
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.creditCardsLookupOptiops.fcName], 0);
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.debitCardsLookupOptiops.fcName], 1);
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.creditCardsLookupOptiops.fcName], 0);
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.debitCardsLookupOptiops.fcName], 1);
      } else
        if (values.itemValue === 'C') {
          this.showCreditCardlookup = true;
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.creditCardsLookupOptiops.fcName], 1);
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.debitCardsLookupOptiops.fcName], 0);
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.creditCardsLookupOptiops.fcName], 1);
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.debitCardsLookupOptiops.fcName], 0);
        }
    }
  }
}